import React from 'react'

import Box from '../components/Box';
import Button from '../components/Button';
import Flex from '../components/Flex';
import Image from '../components/Image';
import Link from '../components/Link';
import { responsive } from '../components/ThemeProvider/theme';

import logo from './logo.svg'

const links = [
  { name: '團隊簡介', to: '/about' },
  { name: '垃圾圖鑑', to: '/catalogue' },
  { name: '丟垃圾大挑戰', to: '/game' },
  { name: '如何把回收做得更好', to: '/how' },
  { name: '贊助我們', to: '/support', isSupport: true },
]

const Header = ({ siteTitle, ...props }) => (
  <Flex
    position="fixed"
    top={0}
    left={0}
    right={0}
    alignItems="center"
    zIndex="docked"
    {...props}
  >
    <Box px={responsive('2em', '1em')} flex={1}>
      <Link to="/">
        <Image width="12em" src={logo} alt="回收大百科" />
      </Link>
    </Box>
    {links.map(({ name, to, isSupport }, i) => (
      <Button
        variant="outline"
        colorScheme="black"
        bg={isSupport ? 'colors.yellow' : 'white'}
        to={to}
        mx="0.5em"
        borderWidth="0.15em"
        key={i}
      >
        {name}
      </Button>
    ))}
    {/* <Box.Absolute bottom="0" left="2em" right="2em" height="0.125em" bg="black" /> */}
  </Flex>
)

export default Header
